import { Injectable } from '@angular/core';

@Injectable()
export class BillpayUtilityService {
  static checkForProperty(property: string | (string | number)[], inObject: any) {
    let propertyAsArray: (string | number)[] = [];
    if (typeof property === 'string') {
      propertyAsArray = property.split('.');
    } else {
      propertyAsArray = property;
    }
    return propertyAsArray.reduce(
      (currentObject: any, currentProperty: any) =>
        currentObject && currentObject[currentProperty] ? currentObject[currentProperty] : undefined,
      inObject,
    );
  }

  static generateId() {
    return Math.floor(Math.random() * Date.now()).toString();
  }
}
